<template>
  <div class="login">
    <h2 class="animated fadeInDown">Your <span class="text-primary">ToDoList</span></h2>
    <img class="animated zoomIn mb-3" :src="logo" alt="">
    <div class="animated fadeInUp">
      <form class="">
        <div class="form-group ">
          <input type="email" class="form-control" aria-describedby="emailHelp" placeholder="Account">
          <!-- <small class="form-text text-muted">We'll never share your email with anyone else.</small> -->
        </div>
        <div class="form-group">
          <input type="password" class="form-control" placeholder="Password">
        </div>
        <button type="submit" class="btn btn-primary">Submit</button>
      </form>
      <hr>
      <button class="btn btn-outline-primary" @click="login('google')">
        <font-awesome-icon fixed-width class="ml-auto text-primary" size="lg" :icon="['fab', 'google']" />
        Use Google Account
      </button>
      <button class="btn btn-outline-primary" @click="logout()">
        logout
      </button>
    </div>
  </div>
</template>

<script>
import firebase from 'firebase'
import logo from '../../static/logo.png'

import { rtdbDBAuth } from '@/service/rtdb_db'
export default {
  data () {
    return {
      logo: logo,
      user: null,
      isAuth: false // Is authorized flag
    }
  },
  methods: {
    login (providerName) {
      let provider = null
      switch (providerName) {
        case 'google':
          provider = new firebase.auth.GoogleAuthProvider()
          break
      }

      rtdbDBAuth.signInWithPopup(provider)
        .then(result => {
          this.user = result.user
          let { displayName, email, emailVerified } = result.user
          return { displayName, email, emailVerified }
        }).then(user => {
          this.$store.commit('setUserInfo', user)
          this.$router.push('/')
        }).catch(err => console.error(err))
    },
    logout () {
      rtdbDBAuth.signOut()
        .then(() => {
          this.user = null
          this.isAuth = false
          window.localStorage.removeItem('user')
          console.log('you will logout')
        }).catch(err => console.error(err))
    }
  },
  beforeCreate () {
    rtdbDBAuth.onAuthStateChanged((user) => {
      if (user) {
        this.user = user
        this.isAuth = true
      }
    })
  },
  created () {
    // console.log(rtdbDBAuth)
  }
}
</script>

<style>
</style>



